<template>
  <g style="filter:url(#dropshadow)" :status="status">
    <g :transform="`translate(${position[0]} ${position[1] + (single ? 15 : 0)})`" :class="single ? 'clipHalf' : 'clipMatch'">
      <half
        :class="{winner: win || single}"
        :id="'player' + seeds[0]"
        :order="{...order, p: 0}"
        :chevron="chevrons[0]"
        :name="names[0]"
        :score="scores[0]"
        :rating="ratings[0]"
        :status="status"
      ></half>
      <half
        v-if="!single"
        :class="{winner: !win}"
        :id="'player' + seeds[1]"
        :order="{...order, p: 1}"
        :chevron="chevrons[1]"
        :name="names[1]"
        :score="scores[1]"
        :rating="ratings[1]"
        :status="status"
        :transform="`translate(0 30.5)`"
      ></half>
    </g>
  </g>
</template>

<script>
import Half from './Half'
export default {
  name: 'Match',
  components: { Half },
  props: {
    order: {
      type: Object,
      default: () => {}
    },
    position: {
      type: Array,
      default: () => [ 0, 0 ]
    },
    chevrons: {
      type: Array,
      default: () => ['#ff0000', '#00ff00']
    },
    seeds: {
      type: Array,
      default: () => []
    },
    names: {
      type: Array,
      default: () => ['Запасной И.', 'Второй И.']
    },
    scores: {
      type: Array,
      default: () => [0, 0]
    },
    ratings: {
      type: Array,
      default: () => ['100', '200']
    },
    status: {
      type: String,
      default: ''
    }
  },
  data: () => ({
  }),
  computed: {
    finished: function () { return !!this.status },
    sign: function () { return this.delta > 0 ? '+' : '-' },
    single: function () { return this.status === 'single' },
    win: function () { return this.status && this.status === 'win' },
    matchWidth: function () { return this.$store.state.settings.matchWidth },
    matchHeight: function () { return this.status === 'single' ? this.$store.state.settings.matchHeight / 2 : this.$store.state.settings.matchHeight }
  }
}
</script>

<style>
.clipMatch {
  clip-path: url(#match);
}
.clipHalf {
  clip-path: url(#half);
}
</style>
