<template>
  <g
    @mouseout="$store.commit('out', {order, $event})"
    @mouseover="$store.commit('over', {order, $event})"
    @click="$store.commit('click', {order, $event})">
    <rect class="player" :width="matchWidth" height="30"></rect>
    <rect class="player--shade" x="35" :width="matchWidth - (single ?  34 : 69)" height="30"></rect>
    <rect class="player--chevron" width="6" height="30" :fill="chevron" v-if="!empty"></rect>
    <text x="19" y="19" width="45" height="12" text-anchor="middle" class="match--rating" v-if="!empty">{{rating}}</text>
    <text x="45" y="21" :width="matchWidth - 90" height="21" class="match--name" v-if="!empty">{{name}}</text>
    <text :x="matchWidth - 18" y="21" width="45" height="21" text-anchor="middle" class="match--result" v-if="started">{{score}}</text>
  </g>
</template>

<script>
export default {
  name: 'Half',
  components: { },
  props: {
    order: {
      type: Object,
      default: () => {}
    },
    chevron: {
      type: [Number, String],
      default: '#ff0000'
    },
    name: {
      type: String,
      default: ''
    },
    score: {
      type: [Number, String],
      default: 3
    },
    rating: {
      type: [Number, String],
      default: 300
    },
    status: {
      type: String,
      default: ''
    }
  },
  data: () => ({
  }),
  computed: {
    empty: function () { return this.status === '' },
    started: function () { return this.status && this.status !== 'ready' },
    finished: function () { return this.status && this.status !== 'progress' },
    sign: function () { return this.delta > 0 ? '+' : '-' },
    single: function () { return this.status === 'single' },
    matchWidth: function () { return this.$store.state.settings.matchWidth }
  }
}
</script>

<style lang="scss">
.championHighlight .champion .player {
  fill: #fff663;
}
.overHighlight .over .player {
  fill: #80ff55;
}
.player {
  fill: #ffffff;
  border-left: 10px solid #DDD;
  cursor: pointer;
  will-change: fill;
  transition: fill .2s ease-in-out;
}
.winner {
  .match--rating, .match--name, .match--result {
    font-weight: 500;
  }
}
.player--shade, .match--rating, .match--name, .match--result, .player--chevron {
  pointer-events: none;
}
.player--shade {
  fill: rgba(0,0,0,0.04);
}
.match--rating {
  font-size: 10px;
}
.match--delta {
  font-size: 8px;
}
</style>
